<template>
  <div class="newFriendItem">
      <img class="portrait" :src="imgSrc">
      <span class="nickname">{{nickname}}<br><span class="message">{{message}}</span></span>
      <button class="btn" v-if="!isAccepted" @click="accept">接受</button>
      <div class="msg" v-else>已添加</div>
  </div>
</template>

<script>
export default {
  data:function(){
      return {
          isAccepted:false,
      }
  },
  methods:{
      accept(){
          this.isAccepted=true;
      }
  },
  props:['imgSrc','nickname','message']
}
</script>

<style scoped>
    .newFriendItem {
        font-size: 1rem;
        border-bottom: 1px solid #ddd;
        background-color: white;
    }
    .portrait {
        padding: 10px 10px 10px 15px;
        height: 50px;
        width: 50px;
        vertical-align: middle;
    }
    .nickname {
        display: inline-block;
        vertical-align: top;
        padding-top: 1rem;
    }
    .message {
        color: #888;
    }
    .btn {
        border: none;
        color: white;
        width: 4rem;
        height: 2rem;
        margin-top: 1rem;
        margin-right: 1rem;
        background-color:rgb(7, 172, 27);
        float: right;
        border-radius: 5px;
    }
    .msg {
        border: none;
        color: #888;
        margin-top: 1.4rem;
        margin-right: 1rem;
        float: right;
    }
</style>


