<template>
  <div class="mine">
      <div class="blank"></div>
      <div class="mineDetail">
        <img src="../../assets/logo.png">
        <img class="img2" src="../../assets/二维码.png" @click="intoCode">
        <div class="content">
          {{myName}}<br>
          <span class="wechatNum">微信号：{{wechatNum}}</span>
        </div>
      </div>
      <div class="blank"></div>
      <mineItem :imgSrc="imgurl[0]" :itemname="itemName[0]"></mineItem>
      <div class="blank"></div>
      <mineItem :imgSrc="imgurl[1]" :itemname="itemName[1]"></mineItem>
      <mineItem :imgSrc="imgurl[2]" :itemname="itemName[2]"></mineItem>
      <mineItem :imgSrc="imgurl[3]" :itemname="itemName[3]"></mineItem>
      <mineItem :imgSrc="imgurl[4]" :itemname="itemName[4]"></mineItem>
      <div class="blank"></div>
      <mineItem :imgSrc="imgurl[5]" :itemname="itemName[5]"></mineItem>
      <div class="last-blank"></div>

  </div>
</template>

<script>
import mineItem from './mineItem'
export default {
  data:function(){
      return {
            imgurl:[
            require('../../assets/钱包.png'),
            require('../../assets/收藏.png'),
            require('../../assets/相册.png'),
            require('../../assets/卡包.png'),
            require('../../assets/表情.png'),
            require('../../assets/设置.png'),
            ],
            itemName:['钱包','收藏','相册','卡包','表情','设置',],
            myName:'Vue',
            wechatNum:'123456789',

      }
  },
  methods:{
    intoCode(){
      console.log('intocode');
      this.$store.dispatch('setForGoForward');
      this.$router.push('/codeDetail');
    }
  },

  props:[],
  components:{
      mineItem
  }
}
</script>

<style scoped>
    .blank {
        height: 1rem;
        clear: both;
    }
    .last-blank {
        height:10rem;
    }
    .mine {
        padding-top: 3rem;
        font-size: 1.3rem;
        background-color: #eee;
    }
    .mineDetail {
      background-color: white;
      overflow: hidden;
    }
    .mineDetail img{
      width: 60px;
      height: 60px;
      border: 1px solid #eee;
      margin: 0.5rem 1rem 0.5rem 1.2rem;
      float: left;
    }
    img.img2 {
      width: 25px;
      height: 25px;
      float: right;
      margin-top: 1.5rem;
      margin-right: 1.5rem;
    }
    .content {
      font-size: 1rem;
      float: left;
      margin-top: 1rem;
      margin-left: .6rem;
    }
    .wechatNum {
      color: #aaa;
      font-size: 0.9rem;
    }
    .alarmImg {
        width: 30px;
        height: 30px;
        float: right;
        margin-top: 0.6rem;
        margin-right: 1.5rem;
    }
</style>







