<template>
    <div class="wfooter">
        <ul>
            <li><a :class="item1" @click="choseOne">微信</a></li>
            <li><a :class="item2" @click="choseTwo">通信录</a></li>
            <li><a :class="item3" @click="choseThree">发现</a></li>
            <li><a :class="item4" @click="choseFour">我</a></li>
        </ul>
    </div>
</template>

<script>
export default {
    data(){
        return {
            item1:'item1 item1Select',
            item2:'item2',
            item3:'item3',
            item4:'item4',
        }
    },
    methods:{
        choseOne(){
            this.$store.dispatch('selectOne');
            this.item1='item1 item1Select';
            this.item2='item2';
            this.item3='item3';
            this.item4='item4';
            this.$router.push('/box/chatwindow');
        },
        choseTwo(){
            this.$store.dispatch('selectTwo');
            this.item2='item2 item2Select';
            this.item1='item1';
            this.item3='item3';
            this.item4='item4';
            this.$router.push('/box/contacts');
        },
        choseThree(){
            this.$store.dispatch('selectThree');
            this.item3='item3 item3Select';
            this.item2='item2';
            this.item1='item1';
            this.item4='item4';
            this.$router.push('/box/discovery');
        },
        choseFour(){
            this.$store.dispatch('selectFour');
            this.item4='item4 item4Select';
            this.item2='item2';
            this.item3='item3';
            this.item1='item1';
            this.$router.push('/box/mine');
        },
    },
    beforeMount:function(){
        let temp=this.$store.getters.getFooterState;
        if (temp[0]) {
            this.item1='item1 item1Select';
            this.item2='item2';
            this.item3='item3';
            this.item4='item4';
        }else if (temp[1]) {
            this.item2='item2 item2Select';
            this.item1='item1';
            this.item3='item3';
            this.item4='item4';
        }else if (temp[2]) {
            this.item3='item3 item3Select';
            this.item2='item2';
            this.item1='item1';
            this.item4='item4';
        }else if (temp[3]) {
            this.item4='item4 item4Select';
            this.item2='item2';
            this.item3='item3';
            this.item1='item1';
        }

    }
}
</script>

<style scoped>
    .wfooter {
        width: 100%;
        height: 4rem;
        position: fixed;
        border-top: 1px solid #bbb;
        bottom: -1px;
        background-color: white;
        z-index: 999;
    }
    ul {
        height: 100%;
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        align-items: stretch;
    }
    ul li {
        width: 25%;
        color: #666;
        font-size: 0.85rem;
        text-align: center;
    }

    ul li a {
        margin-top: 0.3rem;
        padding-top: 32px;
        display: inline-block;
        white-space: nowrap;
        user-select: none;
    }
    .item1 {
        width: 35px;
        height: 32px;
        background-image: url('../assets/icons.png');
        background-repeat: no-repeat;
        background-position: 0 0;
    }
    .item1Select {
        background-position: -35px 0;
        color:rgb(70, 192, 27);
    }
    .item2 {
        width: 35px;
        height: 32px;
        background-image: url('../assets/icons.png');
        background-repeat: no-repeat;
        background-position: -70px 0;
        text-indent: -5px;
    }
    .item2Select {
        background-position: -105px 0;
        color:rgb(70, 192, 27);
    }
    .item3 {
        width: 34px;
        height: 32px;
        background-image: url('../assets/icons.png');
        background-repeat: no-repeat;
        background-position: -140px 0;
    }
    .item3Select {
        background-position: -174px 0;
        color:rgb(70, 192, 27);
    }
    .item4 {
        width: 31px;
        height: 32px;
        background-image: url('../assets/icons.png');
        background-repeat: no-repeat;
        background-position: -208px 0;
    }
    .item4Select {
        background-position: -239px 0;
        color:rgb(70, 192, 27);
    }
</style>





