<template>
<transition>
  <div class="insideHeader">
      <img class="back" src="../../assets/back.png" @click="back">
      <span class="title">{{title}}</span>
      <span class="fKey" @click="dosth">{{fKeyText}}</span>
  </div>
</transition>
</template>

<script>
export default {
  data:function(){
      return {

      }
  },
  methods:{
      back(){
          this.$store.dispatch('setForBack');
          this.$router.go(-1);   
      },
      dosth(){
          if (this.$props.keyType==='newFriends') {
              alert('添加新朋友正在构建中')
          }
      }
  },
  computed:{
      fKeyText(){
        if (this.$props.keyType==='newFriends') {
            return '添加朋友'
        }else if (this.$props.keyType==='codeDetail') {
            return '...'
        }else {
            return '...'
        }
      }
  }
  ,
  props:['title','keyType']
}
</script>

<style scoped>
*{
    padding: 0;
    margin: 0;
}
.insideHeader {
    width: 100%;
    height: 3rem;
    position: fixed;
    top: 0;
    background-color: rgb(57, 58, 63);
    z-index: 900;
    
}
.back {
    margin-top: .6rem;
    margin-left: .5rem;
    width:30px;
    height: 30px;
    vertical-align: top;
}
span.title {
    display: inline-block;
    color: white;
    font-size: 1.2rem;
    border-left: 1px solid #222;
    margin-left: .5rem;
    padding-left: 1rem;
    vertical-align: top;
    margin-top: .7rem;
    
}
span.fKey {
    display: block;
    color: white;
    font-size: 1rem;
    border-left: 1px solid #222;
    padding-left: .5rem;
    margin-top: .7rem;
    margin-right: .6rem;
    float: right;
}

</style>





