<template>
  <div id="app">
    <transition :name="name" :mode="mode" v-on:before-enter="beforeEnter">
    <router-view></router-view>
    </transition>
  </div>
</template>


<script>
export default {
  data(){
    return {
      name:'fade',
      mode:'out-in'
    }
  },
  name: 'app',
  methods:{
    beforeEnter(){
      //这是transition变化前的钩子函数
    }
  },
  components:{
    
  },
  watch: {
  '$route' () {
    this.name=this.$store.getters.getTransMode;
  }
}
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0
}


.rightIn-enter-active {
  animation: right-in .3s linear;
}
@keyframes right-in {
  0% {
    transform: translateX(100%);
  }
  50% {
    transform: translateX(50%);
  }
  100% {
    transform: translateX(0);
  }
}



.leftIn-enter-active {
  animation: left-in .3s linear;
}
@keyframes left-in {
  0% {
    transform: translateX(-100%);
  }
  50% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0);
  }
}

</style>



