<template>
        <div class="newFriends">
            <inside-header title="新的朋友" keyType="newFriends"></inside-header>
            <div class="main">
                <input type="search" placeholder="微信号/QQ号/手机号" class="search">
                <div class="title">新的朋友</div>
                <new-friend-item v-for="item in items" :key="item.id" :imgSrc='imgurl' :nickname="nickname" :message="message"></new-friend-item>
            </div>
        </div>
</template>

<script>
import newFriendItem from './newFriendItem'
import insideHeader from './insideHeader'
export default {
    data: function() {
        return {
            items:[1,2,3,4,5],
            imgurl:require('../../assets/logo.png'),
            nickname:'新朋友的昵称',
            message:'您好认识一下好么'
        }
    },
    methods: {
    },
    props: {

    },
    components: {
        insideHeader,
        newFriendItem

    }
}
</script>

<style scoped>
.newFriends {
    background-color: white;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 1001;
    top: 0;
}

.main {
    margin-top: 3rem;
    background-color: #eee;
}

.search {
    width: 100%;
    outline: none;
    height: 2rem;
    border: none;
    border-bottom: 1px solid #aaa;
    margin: .5rem;
    padding: .5rem;
}

.title {
    width: 100%;
    height: 1.5rem;
    background-color: #eee;
    font-size: .8rem;
    color: #777;
    text-indent: 0.8rem;
    line-height: 1.5rem;
}
</style>





