<template>
  <div class="contactGroup">
      <div class="title" :id="titleId">{{title}}</div>
      <contactbrief v-for="item in items" :key="item.id" :imgSrc="imgurl" :nickname="nickname"></contactbrief>
  </div>
</template>

<script>
import contactbrief from './contactBrief'
export default {
  data:function(){
      return {
          items:[1,2,3,4,5],
          imgurl:require('../../assets/logo.png'),
          nickname:'用户的昵称'+this.title+'组',
      }
  },
  computed:{
      titleId(){
          return this.title+'Id';
      }
  },
  components:{
      contactbrief
  },
  props:['title']
}
</script>

<style scoped>
    .contactGroup {
        width: 100%;
    }
    .title {
        width: 100%;
        height: 1.5rem;
        background-color: #eee;
        font-size: 1rem;
        color: #777;
        text-indent: 0.8rem;
        line-height: 1.5rem;
    }
</style>





