<template>
    <div class="codeDetail">
        <inside-header title="二维码名片" keyType="codeDetail"></inside-header>
        <div class="main">
            <div class="card">
                <div class="person">
                    <img class="portrait" src="../../assets/logo.png" alt="portrait">
                    <div class="text">
                        <span class="nickname">VueWX</span><br>
                        <span class="city">浙江 杭州</span>
                    </div>
                </div>
                <div class="code">
                    <img class="codeImg" src="../../assets/二维码.png" alt="">
                </div>
                <div class="msg">扫一扫上面的二维码团，加我微信</div>
            </div>
        </div>
    </div>
</template>

<script>
import insideHeader from '../others/insideHeader'
export default {
    data: function() {
        return {

        }
    },
    methods: {
    },
    props: {

    },
    components: {
        insideHeader,
    }
}
</script>

<style scoped>
.codeDetail {
    background-color: white;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 1001;
    top: 0;
}

.nickname {
    vertical-align: top;
}

.city {
    display: inline-block;
    vertical-align: top;
    color: #aaa;
    margin-top: .5rem;
}

.main {
    margin-top: 3rem;
    background-color: #333;
    overflow: hidden;
    height: 95vh;
}

.card {
    width: 80%;
    margin: 0 auto;
    margin-top: 2rem;
    padding: 1rem;
    background-color: white;
}
.text {
    display: inline-block;
    vertical-align: top;
    padding-top: 1.5rem;
}
.portrait {
    width: 4rem;
    height: 4rem;
    margin: 1rem;
}

.code {
    width: 80%;
    margin: 0 auto;
}

.codeImg {
    display: block;
    margin: 0 auto;
    width: 100%;
}

.msg {
    text-align: center;
    color: #aaa;
}
</style>





