<template>
  <div class="chatBrief">
    <img class="portrait" src="../../assets/logo.png" @click.stop="intoChat">
    <div class="content" @click.stop="intoChat">
        <div class="upItem">
            <span class="nickname">{{person.nickname}}</span>
        </div>
        <span class="brief">{{brief}}</span>
    </div>
        <span class="time">{{time}}</span>
      <chatPopup :nickname="person.nickname"></chatPopup>
  </div>
</template>

<script>
import chatPopup from '../others/chatPopup'
export default {
  data:function(){
      return {
        time:'11:13',
        brief:'对方：你们明天来么？',
      }
  },
  props:['person']
  ,
  methods:{
      intoChat(){
          this.$children[0].isPopup=true;
      }
  },
  components:{
      chatPopup
  }
}
</script>

<style scoped>
    .chatBrief {       
        border-bottom: .5px solid #aaa;   
        overflow: hidden;
    }
    .portrait {
        height: 3.5rem;
        width: 3.5rem;
        margin: 0.6rem;
        margin-left: 1rem;
        float: left;
    }
    .content {
        width: 60%;
        margin: 0.8rem;
        float: left;
    }

    @media screen and (max-width:400px) {
        .content {
        width: 50%;
        }
    }
        
    .upItem {
        width: 100%;
        overflow: hidden;
    }

    .nickname {
        float: left;
        display: block;
        font-size: 1rem;
    }
    .time {
        float: right;
        display: block;
        font-size: 0.8rem;
        color: #888;
        margin-top: .6rem;
        margin-right: 1rem;
    }
    .brief {
        clear: both;
        display: block;
        font-size: 0.8rem;
        color: #888;
        margin-top: 10px;
    }
</style>





