<template>
<transition name="rightInOut">
  <div class="chatPopup" v-show="isPopup">
    <div class="header">
      <img class="back" @click="back" src="../../assets/back.png">
      <span class="titlename">{{nickname}}</span>
      <img class="detail" src="../../assets/detail.png" alt="detail">
    </div>
      这是{{nickname}}的聊天页面<br>
      下方对话暂时用图片模拟布局，未实现<br>
      其他分页的弹出效果也类似，暂未加入
  
  <div>
    <img class="test" src="../../assets/模拟.png" alt="">
  </div>
  </div>
</transition>
</template>

<script>
export default {
  data:function(){
      return {
          isPopup:false
      }
  },
  methods:{
      back(){
          this.isPopup=false;
      }
  },
  props:['nickname']
}
</script>

<style scoped>


  .header {
        width: 100%;
        height: 3rem;
        background-color: rgb(57, 58, 63);
  }
  .titlename {
    color: white;
    line-height: 3rem;
    display: inline-block;
    margin-left: 1rem;
    font-size: 1rem;
  }
  .header img {
    width:30px;
    height: 30px;
  }

  .back {
    float: left;
    margin-top: .5rem;
    margin-left: .6rem;
    padding-right: .5rem;
    border-right: 1px solid #222;
  }
  .detail {
    float: right;
    margin-top: .5rem;
    margin-right: .6rem;
  }

    .chatPopup {
        background-color: white;
        width: 100vw;
        height: 100vh;
        position: fixed;
        z-index: 1001;
        top: 0;
    }

    .test {
      width: 100vw;
      position: absolute;
      bottom: 0;
    }

.rightInOut-enter-active {
  animation: right-in .3s linear;
}
.rightInOut-leave-active {
  animation: right-in .3s reverse linear;
}
@keyframes right-in {
  0% {
    transform: translateX(100%);
  }
  50% {
    transform: translateX(50%);
  }
  100% {
    transform: translateX(0);
  }
}
</style>





