import Vue from 'vue'
import Router from 'vue-router'
import chatwindow from '../components/chats/chatWindow.vue'
import contacts from '../components/contacts/contacts.vue'
import discoveryWindow from '../components/discovery/diccoveryWindow.vue'
import mine from '../components/mine/mine.vue'
import box from '../Box.vue'
import searchpage from '../components/others/searchPage.vue'
import newFriends from '../components/others/newFriends.vue'
import codeDetail from '../components/mine/codeDetail.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/box/:id',
      name:"box",
      component: box,
      children:[
        {
          path:'/box/chatwindow',
          name:"chatwindow",
          component: chatwindow,
 
        },
        {
          path:'/box/contacts',
          name:"contacts",
          component: contacts
        },
        {
          path:'/box/discovery',
          name:"discovery",
          component: discoveryWindow
        },
        {
          path:'/box/mine',
          name:"mine",
          component: mine
        }
      ]
    },
    {
      path:'/',
      redirect:'/box/chatwindow'
    },
    {
      path:'/search',
      name:"search",
      component:searchpage
    },
    {
      path:'/newFriends',
      name:'newFriends',
      component:newFriends
    },
    {
      path:'/codeDetail',
      name:'codeDetail',
      component:codeDetail
    }
]
})



