<template>
  <div class="mineItem">
      <img class="itemImg" :src="imgSrc">
      <span class="itemName">{{itemname}}</span>
      <slot></slot>
  </div>
</template>

<script>
export default {
  data:function(){
      return {
      }
  },
  props:['imgSrc','itemname']
}
</script>

<style scoped>
    .mineItem {
        font-size: 1rem;
        border-bottom: 1px solid #eee;
        background-color: white;
    }
    .itemImg {
        padding: 0.8rem 0.8rem 0.8rem 1rem;
        height: 30px;
        width: 30px;
        vertical-align: middle;
    }
</style>





