import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        footerState:[1,0,0,0],
        transitionMode:'fade',
    },
    getters:{
        getFooterState(state){
            return state.footerState;
        },
        getTransMode(state){
            return state.transitionMode;
        }
    },
    mutations: {
        CHOOSE_ONE(state){
            state.footerState=[1,0,0,0]
        },
        CHOOSE_TWO(state){
            state.footerState=[0,1,0,0]
        },
        CHOOSE_THREE(state){
            state.footerState=[0,0,1,0]
        },
        CHOOSE_FOUR(state){
            state.footerState=[0,0,0,1]
        },
        SETFADE(state) {
            state.transitionMode='fade'
        },
        SETRIGHTIN(state){
            state.transitionMode='rightIn'
        },
        SETLEFTIN(state){
            state.transitionMode='leftIn'
        }
    },
    actions: {
        selectOne(store){
            store.commit('CHOOSE_ONE');
        },
        selectTwo(store){
            store.commit('CHOOSE_TWO');
        },
        selectThree(store){
            store.commit('CHOOSE_THREE');
        },
        selectFour(store){
            store.commit('CHOOSE_FOUR');
        },
        setForSearch(store) {
            store.commit('SETFADE');
        },
        setForGoForward(store){
            store.commit('SETRIGHTIN');
        },
        setForBack(store){
            store.commit('SETLEFTIN');
        }
    }
})


