<template>
  <div class="discoveryWindow">
      <div class="blank"></div>
      <discoveryItem :imgSrc="imgurl[0]" :itemname="itemName[0]"><img class="alarmImg" src="../../assets/logo.png"></discoveryItem>
      <div class="blank"></div>
      <discoveryItem :imgSrc="imgurl[1]" :itemname="itemName[1]"></discoveryItem>
      <discoveryItem :imgSrc="imgurl[2]" :itemname="itemName[2]"></discoveryItem>
      <div class="blank"></div>
      <discoveryItem :imgSrc="imgurl[3]" :itemname="itemName[3]"></discoveryItem>
      <discoveryItem :imgSrc="imgurl[4]" :itemname="itemName[4]"></discoveryItem>
      <div class="blank"></div>
      <discoveryItem :imgSrc="imgurl[5]" :itemname="itemName[5]"></discoveryItem>
      <discoveryItem :imgSrc="imgurl[6]" :itemname="itemName[6]"><img class="alarmImg" src="../../assets/游戏图标.png"></discoveryItem>
      <div class="blank"></div>
      <discoveryItem :imgSrc="imgurl[7]" :itemname="itemName[7]"></discoveryItem>
      <div class="last-blank"></div>

  </div>
</template>

<script>
import discoveryItem from './discoveryItem'
export default {
  data:function(){
      return {
            imgurl:[
            require('../../assets/朋友圈.png'),
            require('../../assets/扫一扫.png'),
            require('../../assets/摇一摇.png'),
            require('../../assets/附近的人.png'),
            require('../../assets/漂流瓶.png'),
            require('../../assets/购物.png'),
            require('../../assets/游戏.png'),
            require('../../assets/小程序.png'),
            ],
            itemName:['朋友圈','扫一扫','摇一摇','附近的人','漂流瓶','购物','游戏','小程序']
      }
  },
  props:[],
  components:{
      discoveryItem
  }
}
</script>

<style scoped>
    .blank {
        height: 1rem;
    }
    .last-blank {
        height:10rem;
    }
    .discoveryWindow {
        padding-top: 3rem;
        height: 100%;
        font-size: 1.3rem;
        background-color: #eee;
    }
    .alarmImg {
        width: 30px;
        height: 30px;
        float: right;
        margin-top: 0.6rem;
        margin-right: 1.5rem;
    }
</style>





