<template>
    <div class="wheader">
        <p id="title">微信{{newMagStr}}</p>
        <a class="plusIcon" @click="plusPage"></a>
        <transition name="upDown">
            <div class="plusDown" v-show="isDown">
                <ul>
                    <li :class="isUp">发起群聊</li>
                    <li :class="isUp">添加朋友</li>
                    <li :class="isUp">扫一扫</li>
                    <li :class="isUp">收付款</li>
                    <li :class="isUp">帮助与反馈</li>
                </ul>
            </div>
        </transition>
        <a class="searchIcon" @click="searchPage"></a>

    </div>
</template>

<script>
export default {
    data: function() {
        return {
            newMagNum: 5,
            isDown: false,
        }
    },
    computed: {
        newMagStr: function() {
            if (this.newMagNum === 0) return '';
            else return '( ' + this.newMagNum + ' )';
        },
        isUp:function(){
            if(this.isDown) return ''
            else return 'pullUp'
        }
    },
    methods: {
        searchPage() {
            this.$store.dispatch('setForSearch');
            this.$router.push('/search');
        },
        plusPage() {
            this.isDown = !this.isDown;
        }
    }
}
</script>

<style scoped>
.wheader {
    width: 100%;
    height: 3rem;
    position: fixed;
    top: 0;
    background-color: rgb(57, 58, 63);
    z-index: 1000;
}

.plusDown {
    width: 50%;
    background-color: rgb(57, 58, 63);
    position: absolute;
    top: 3rem;
    right: 1rem;
    height: 15rem;
}

.plusDown ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.plusDown ul li {
    display: block;
    height: 3rem;
    box-sizing: border-box;
    border-top: 1px solid #333;
    color: white;
    line-height: 3rem;
    text-indent: 4rem;
}

.plusDown ul li:nth-child(1) {
    background-image: url('../assets/upList-1.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 3% 0;
}

.plusDown ul li:nth-child(2) {
    background-image: url('../assets/upList-2.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 3% 0;
}

.plusDown ul li:nth-child(3) {
    background-image: url('../assets/upList-3.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 3% 0;
}

.plusDown ul li:nth-child(4) {
    background-image: url('../assets/upList-4.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 3% 0;
}

.plusDown ul li:nth-child(5) {
    background-image: url('../assets/upList-5.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 3% 0;
}

.plusDown ul li.pullUp {
    background:none;
}

#title {
    color: white;
    font-size: 1.2rem;
    float: left;
    margin-left: 1rem;
    margin-top: 0.6rem;
}

.plusIcon {
    margin: 0.2rem 1rem 0 1rem;
    float: right;
    display: block;
    width: 32px;
    height: 32px;
    background-image: url('../assets/icons.png');
    background-repeat: no-repeat;
    background-position: -298px 0;
}

.searchIcon {
    margin: 0.2rem 0.3rem 0 0;
    float: right;
    display: block;
    width: 30px;
    height: 32px;
    background-image: url('../assets/icons.png');
    background-repeat: no-repeat;
    background-position: -268px 0;
}

.upDown-enter-active {
    transition: all .3s;
}

.upDown-leave-active {
    transition: all .3s;
}

.upDown-enter,
.upDown-leave-to {
    width: 0;
    opacity: 0;
    height: 0;
}
</style>





