<template>
    <div class="search">
        <div class="header">
        <img class="back" src="../../assets/back.png" @click="back">
        <input type="search" class="searchIcon" placeholder="搜索">
        </div>
        <img class="searchImg" src="../../assets/search.png" alt="asForSearchPage">
    </div>
</template>

<script>
export default {
  data:function(){
      return {
      }
  },
  methods:{
      back(){
          this.$router.go(-1);
      }
  },
}
</script>

<style scoped>


  .header {
        width: 100%;
        height: 3rem;
        background-color: rgb(57, 58, 63);
  }
  .header img {
    width:30px;
    height: 30px;
  }

  .back {
    float: left;
    margin-top: .5rem;
    margin-left: .6rem;
    padding-right: .5rem;
    border-right: 1px solid #222;
  }
    .search {
        background-color: white;
        width: 100vw;
        height: 100vh;
        position: fixed;
        z-index: 1001;
        top: 0;
        overflow: hidden;
    }
    .searchIcon {
        width: 80%;
        height: 2rem;
        background-image: url('../../assets/searchIcon.png');
        background-repeat: no-repeat;
        background-position: 0 0;
        border: none;
        background-color: rgb(57, 58, 63);
        border-bottom: 1px solid green;
        margin-top: .6rem;
        margin-left: 1rem;
        font-size: 1.2rem;
        text-indent: 2.5rem;
        outline: none;
    }

    .searchIcon:focus {
        color: white;
    }
    .searchImg {
        width: 100%;
        height: 100%;
    }
</style>





